import React, { Component } from "react";
import "./App.css";
import { Detector } from "react-detect-offline";
export class App extends Component {
  state = {
    songs: [
      {
        Like: true,
        songName: "Pubg Lets Go",
        artist: "Pratik Deshmukh",
        link:
          "https://github.com/pratikdeshmukh2004/Audio-Player/blob/master/pubg_best_ringtone.mp3?raw=true",
        thumbnail:
          "data:image/jpeg;base64,",
      },
      {
        Like: true,
        songName: "Darkside",
        artist: "Alan Walker",
        link:
          "https://github.com/pratikdeshmukh2004/Audio-Player/blob/master/Darkside.mp3?raw=true",
        thumbnail:
          "https://dl.dropbox.com/s/brlk8ano6w6qzd9/wp2383971-alan-walker-logo-wallpapers.jpg?raw=1",
      },

      {
        Like: false,
        songName: "Sing Me To Sleep",
        artist: "Alan Walker",
        link:
          "https://dl.dropbox.com/s/8eml5r6ky35pwsc/Alan%20Walker%20-%20Sing%20Me%20to%20Sleep%20-%20MP3%20320.mp3?raw=1",
        thumbnail:
          "https://dl.dropbox.com/s/enuh5g9zfli7plm/wp2383907-alan-walker-logo-wallpapers.jpg?raw=1",
      },

      {
        Like: false,
        songName: "On My Way",
        artist: "Alan Walker",
        link:
          "https://dl.dropbox.com/s/8jngsfgicfwcz3i/Alan%20Walker%2C%20Sabrina%20Carpenter%20%26%20Farruko%20%20-%20On%20My%20Way.mp3?raw=1",
        thumbnail:
          "https://dl.dropbox.com/s/hz64cpmd7icftc8/wp4236603-alan-walker-on-my-way-wallpapers.jpg?raw=1",
      },

      {
        Like: false,
        songName: "Faded",
        artist: "Alan Walker",
        link:
          "https://dl.dropbox.com/s/akk34b59swgauvu/Faded%20%28Alan%20Walker%29%20320Kbps-%28GanaMirchi.in%29.mp3?raw=1",
        thumbnail:
          "http://a10.gaanacdn.com/images/albums/62/1846262/crop_480x480_1846262.jpg",
      },

      {
        Like: false,
        songName: "Ignite",
        artist: "Alan Walker",
        link:
          "https://dl.dropbox.com/s/zh6msl6zz46qb3e/K-391%20%20Alan%20Walker%20-%20Ignite%20%28feat.%20Julie%20Bergan%20%20Seungri%29.mp3?raw=1",
        thumbnail:
          "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRJUUmqUuRIOhJJ3WzIhDUUdznTHJjY9ZqEa5ov8vbB9k-zL0rS",
      },

      {
        Like: true,
        songName: "Alone",
        artist: "Alan Walker",
        link:
          "https://dl.dropbox.com/s/8zxdi4wgqplyny0/Alone-%28MrPaji.com%29.mp3?raw=1",
        thumbnail:
          "https://dl.dropbox.com/s/69u5dxjkxoi0fyq/wp4236570-alan-walker-on-my-way-wallpapers.png?raw=1",
      },
    ],
    isPlaying: false,
    songIndex: 0,
    image: "",
    songurl: "",
    artist: "",
    songName: "",
    loader: "block",
    app: "none",
    sidebarIcon: "fa fa-chevron-left",
    audio: "",
    Like: true,
    duration: "0.0",
    currentTime: "0.0",
  };

  componentDidMount() {
    var song = this.state.songs[0];
    this.setState({
      image: song.thumbnail,
      songName: song.songName,
      artist: song.artist,
      songurl: song.link,
      audio: new Audio(song.link),
      Like: song.Like,
    });
    setTimeout(() => {
      document.getElementById("Sidebar").classList.toggle("active");
      this.setState({
        loader: "none",
        app: "block",
      });
    }, 5000);
    document.addEventListener("keydown", this.onKeyDown);
  }

  onKeyDown = (e) => {
    e = e || window.event;
    switch (e.keyCode) {
      case 38:
        if (this.state.audio.volume < 1) {
          this.state.audio.volume = (this.state.audio.volume + 0.1).toString();
        }
        break;
      case 40:
        if (this.state.audio.volume > 0.1) {
          this.state.audio.volume = (this.state.audio.volume - 0.1).toString();
        }
        break;
      case 37:
        this.onPrew();
        break;
      case 39:
        this.onNext();
        break;
      case 32:
        if (this.state.isPlaying) {
          this.setState({
            isPlaying: false,
          });
          this.Pause();
        } else {
          this.setState({
            isPlaying: true,
          });
          this.Play();
        }
        break;
    }
  };

  showSidebar = (e) => {
    document.getElementById("Sidebar").classList.toggle("active");
    if (this.state.sidebarIcon == "fa fa-chevron-right") {
      this.setState({
        sidebarIcon: "fa fa-chevron-left",
      });
    } else {
      this.setState({
        sidebarIcon: "fa fa-chevron-right",
      });
    }
  };

  Play = () => {
    var audio = this.state.audio;
    audio.addEventListener("timeupdate", (e) => {
      this.setState({
        currentTime: e.target.currentTime,
        duration: e.target.duration,
      });
    });
    audio.play();
  };

  Pause = () => {
    var audio = this.state.audio;
    audio.addEventListener("timeupdate", (e) => {
      this.setState({
        currentTime: e.target.currentTime,
        duration: e.target.duration,
      });
    });
    audio.pause();
  };

  onPlay = () => {
    var isPlaying = this.state.isPlaying;
    if (isPlaying) {
      this.setState({
        isPlaying: false,
      });
      this.Pause();
    } else {
      this.setState({
        isPlaying: true,
      });
      this.Play();
    }
  };
  onNext = () => {
    this.state.audio.pause();
    if (this.state.songIndex !== this.state.songs.length - 1) {
      var song = this.state.songs[this.state.songIndex + 1];
      var newsong = new Audio(song.link);
      this.setState({
        songIndex: this.state.songIndex + 1,
        image: song.thumbnail,
        songurl: song.link,
        artist: song.artist,
        songName: song.songName,
        audio: newsong,
        Like: song.Like,
        duration: "0.0",
        currentTime: "0.0",
      });
      if (this.state.isPlaying) {
        newsong.play();
      }
      newsong.addEventListener("timeupdate", (e) => {
        this.setState({
          currentTime: e.target.currentTime,
          duration: e.target.duration,
        });
      });
    }
  };

  onPrew = () => {
    this.state.audio.pause();
    if (this.state.songIndex !== 0) {
      var song = this.state.songs[this.state.songIndex - 1];
      var newsong = new Audio(song.link);
      this.setState({
        songIndex: this.state.songIndex - 1,
        image: song.thumbnail,
        songurl: song.link,
        artist: song.artist,
        songName: song.songName,
        audio: newsong,
        Like: song.Like,
        duration: "0.0",
        currentTime: "0.0",
      });
      if (this.state.isPlaying) {
        newsong.play();
      }
      newsong.addEventListener("timeupdate", (e) => {
        this.setState({
          currentTime: e.target.currentTime,
          duration: e.target.duration,
        });
      });
    }
  };
  onSelect = (e) => {
    var id = parseInt(e.target.id);
    this.state.audio.pause();
    var song = this.state.songs[id];
    var newsong = new Audio(song.link);
    this.setState({
      songIndex: id,
      image: song.thumbnail,
      songurl: song.link,
      artist: song.artist,
      songName: song.songName,
      audio: newsong,
      Like: song.Like,
      currentTime: "0.0",
      duration: "0.0",
    });
    if (this.state.isPlaying) {
      newsong.play();
    }
    newsong.addEventListener("timeupdate", (e) => {
      this.setState({
        currentTime: e.target.currentTime,
        duration: e.target.duration,
      });
    });
  };
  onLike = () => {
    var main = this.state.songs;
    var isliked;
    if (this.state.Like) {
      isliked = false;
    } else {
      isliked = true;
    }
    main[this.state.songIndex].Like = isliked;
    this.setState({
      songs: main,
      Like: isliked,
    });
  };

  componentDidUpdate() {
    var ctime =
      Math.floor((this.state.currentTime % 3600) / 60) +
      "." +
      Math.floor(this.state.currentTime % 60);
    var duration =
      Math.floor((this.state.duration % 3600) / 60) +
      "." +
      Math.floor(this.state.duration % 60);
    if (ctime == duration && ctime !== "0.0") {
      this.setState({
        currentTime: "0.0",
        isPlaying: true,
      });
      this.onNext();
    }
  }
  render() {
    document.title = this.state.songName;
    var ctime =
      Math.floor((this.state.currentTime % 3600) / 60) +
      "." +
      Math.floor(this.state.currentTime % 60);
    var duration =
      Math.floor((this.state.duration % 3600) / 60) +
      "." +
      Math.floor(this.state.duration % 60);
    var percentage = Math.floor(
      (this.state.currentTime / this.state.duration) * 100
    );
    return (
      <div className="App">
        <div id="loader" style={{ display: this.state.loader }}>
          <div class="pair pair-1">
            <div class="dot dot-1"></div>
            <div class="dot dot-2"></div>
          </div>
          <div class="pair pair-2">
            <div class="dot dot-1"></div>
            <div class="dot dot-2"></div>
          </div>
          <div class="text">Loading</div>
          <span id="android" class="fa fa-android" aria-hidden="true"></span>
          <span id="apple" class="fa fa-apple" aria-hidden="true"></span>
          <span id="copyright" aria-hidden="true">
            ©mp3_2019
          </span>
        </div>

        <div id="app" style={{ display: this.state.app }}>
          <span
            id="about"
            className="fa fa-th"
            style={{ fontSize: "28px" }}
            onclick="about()"
          ></span>

          <div class="song-details">
            <div class="song-pic">
              <img src={this.state.image} id="thumbnail" class="act-img" />
            </div>
          </div>

          <div id="song-name">{this.state.songName}</div>
          <div id="artist-name">{ctime + " : " + duration}</div>
          <div id="loadbar">
            <div class="progress">
              <span
                class="progress-bar"
                style={{ width: percentage + "%" }}
              ></span>
            </div>
          </div>
          <div class="button-control">
            <Detector
              render={({ online }) => (
                <div id="shuffle">
                  <i
                    class="fa fa-wifi"
                    style={online ? { color: "green" } : { color: "red" }}
                    aria-hidden="true"
                  ></i>
                </div>
              )}
            />
            <span
              id="prev"
              class="fa fa-step-backward"
              onClick={this.onPrew}
            ></span>
            <span
              id="play"
              onClick={this.onPlay}
              class={this.state.isPlaying ? "fa fa-pause" : "fa fa-play"}
            ></span>
            <span
              id="next"
              onClick={this.onNext}
              class="fa fa-step-forward"
            ></span>
            <span
              id="fav"
              onClick={this.onLike}
              style={this.state.Like ? { color: "red" } : { color: "white" }}
              class="fa fa-heart"
            ></span>
          </div>

          <div id="Sidebar">
            <div class="Tg_Button">
              <span
                onClick={this.showSidebar}
                style={{ fontSize: "25px" }}
                class={this.state.sidebarIcon}
              ></span>
            </div>
            <ol>
              <li style={{ color: "yellow" }}>Song List</li>
              {this.state.songs.map((item, index) => {
                var active = { color: "red" };
                if (this.state.songIndex == index) {
                  return (
                    <li
                      className="activeitem"
                      style={active}
                      onClick={this.onSelect}
                      id={index}
                    >
                      {item.songName}
                    </li>
                  );
                } else {
                  return (
                    <li onClick={this.onSelect} id={index}>
                      {item.songName}
                    </li>
                  );
                }
              })}
            </ol>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
